.skills {
    &__content-wrapper {
        display: flex;
        padding: 20px;
        gap: 20px;

        &__inner-content {
            flex: 1;
            display: flex;
            flex-direction: column;
            align-items: center;


            &__category-text {
                display: inline-block;
                color: var(--yellow-theme-main-color);
                font-size: 22px;
                font-weight: 700;
                line-height: 1rem;
                letter-spacing: 0.5px;
                position: relative;
                text-transform: uppercase;
                padding: 5px 10px;
                border-bottom: 2px solid var(--yellow-theme-main-color);
                margin-right: auto;
            }

            &__category-text::before,
            &__category-text::after {
                position: absolute;
                content: '';
                width: 2px;
                height: 5px;
                background-color: var(--yellow-theme-main-color);
                bottom: -1px;
            }

            &__category-text::before {
                left: 0;
            }

            &__category-text::after {
                right: 0;
            }

            &__progressbar-container {
                width: 100%;
            }

            .progressbar-wrapper {
                // padding : 50px 0px 0px 0px;
                padding-top: 40px;
                width: 190px;

                p {
                    font-size: 1.6rem;
                    margin: 0 0 10px;
                    font-weight: 500;
                    text-transform: uppercase;
                    color: var(--yellow-theme-sub-text-color);
                }
            }

        }
    }
}

@media only screen and (max-width : 850px) {
    .skills__content-wrapper__inner-content .progressbar-wrapper {
        padding-top: 40px;
        width: 150px;
    }
}

@media only screen and (max-width : 740px) {

    .skills__content-wrapper__inner-content__category-text {
        font-size: 18px;
        font-weight: 700;
        line-height: .8rem;
    }

    .skills__content-wrapper__inner-content .progressbar-wrapper {
        padding-top: 40px;
        width: 120px;
    }
}

@media only screen and (max-width : 640px) {
    .skills {
        &__content-wrapper {
            flex-direction: column
        }
    }

    .skills__content-wrapper__inner-content__progressbar-container {
        margin-bottom: 20px;
    }

    .skills__content-wrapper__inner-content__category-text {
        font-size: 22px;
        font-weight: 700;
        line-height: 1rem;
    }

    .skills__content-wrapper__inner-content .progressbar-wrapper {
        padding-top: 40px;
        width: 400px;
    }
}
@media only screen and (max-width : 460px){
    .skills__content-wrapper__inner-content .progressbar-wrapper {
        width: 300px;
    }
}